#blog {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @WXGA-display {
    padding: 40px 75px 14px 50px;
  }

  @media @mobile {
    background-size: cover;
    padding: 0px;
  }



  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    // padding-top: 10vh;
    padding: 60px 0 0 0;

    @media @mobile {
      padding: 0;
    }

    & > * {
      // flex: 1
    }

    .headline {
      color: #249fbe;
      margin-top: -25px;
      font-size: 80.5px;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      letter-spacing: 0.025em;
      margin-bottom: 0;
      // margin-top: 20px;

      @media @WXGA-display {
        margin-top: -15px;
        font-size: 65px;
        margin-left: 124px;
        margin-bottom: 30px;
      }

      @media @mobile {
        margin-top: 60px;
        font-size: 250% !important;
        padding: 0 0 10px 10px;
        // padding: 80px 0 10px 10px;
      }
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      height: 100%;

      @media @mobile {
        padding: 0
      }

      & > * {
        // display: flex;
        // flex: 1
      }

      .divider {
        flex: 1;
      }



      & > .content {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: center;
        overflow: hidden;
        position: relative;
        height: 35em;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;

        @media @mobile {
          overflow: scroll;
        }

        article {
          display: flex;
          width: 80%;
          align-items: flex-start;
          flex-shrink: 0;

          @media @mobile {
            margin-bottom: 30px;
            width: 100%;
            padding: 0 15px;
            flex-direction: column;
          }

          &:first-child {
            padding-bottom: 60px;
          }

          &:not(:first-child) {
            padding: 60px 0;

            @media @mobile {
              padding: 0 15px;
            }
          }

          .header-mobile {
            display: none;

            @media @mobile {
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 14px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
            }
          }

          .image {
            @media @mobile {
              flex: 1;
            }

            img {
              @media @mobile {
                width: 100%;
              }
            }
          }

          .content {
            display: flex;
            margin-left: 130px;
            padding: 0;

            @media @WXGA-display {
              margin-left: 50px;
            }

            @media @mobile {
              flex: 1;
              margin: 0;
            }

            h4 {
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;

              @media @WXGA-display {
                font-size: 28px;
                letter-spacing: 0.025em;
              }

              @media @mobile {
                font-size: 14px;
                display: none;
              }
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;

              @media @mobile {
                font-size: 14px;
              }
            }

            .footer {
              display: flex;
              font-family: 'Proxima Nova';
              font-size: 17px;
              flex: 1;
              justify-content: space-between;
              width: 200px;
              align-self: baseline;

              @media @mobile {
                flex-direction: row;
                font-size: 12px;
                justify-content: flex-start;
              }

              .date {
                color: #b1ccd2;
              }

              a {
                color: #1f1f1f;

                @media @mobile {
                  margin-left: 10px;
                }

                &:hover {
                  color: #ff7d00;
                }
              }
            }
          }
        }
      }
    }
  }
}

