#expertise {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  // height: 100vh;
  padding: 40px 75px 25px 50px;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @WXGA-display {
    padding: 40px 75px 14px 42px;
  }

   @media @mobile {
    padding: 35px 20px 20px 20px;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      padding: 120px 10px 10px 0;
      height: 100%;

      @media @WXGA-display {
        padding-top: 46px;
      }

      @media @mobile {
        padding: 50px 0px 0px 0;
      }

      .divider {
        flex: 1;
      }

      .headline {
        color: #249fbe;
        margin-top: 22px;
        font-size: 80.5px;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction:column;
        align-items:flex-start;
        flex: 2;
        margin-left: 238px;


        // align-self: flex-end;

        @media @WXGA-display {
          margin-left: 133px;
          font-size: 72.5px;
          letter-spacing: -0.01em;
        }

        @media @mobile {
          font-size: 330% !important;
          padding: 0 0 25px 15px;
          align-items: flex-start;
          margin-left: -20px;
        }
      }

      section.content {
        // display: flex;
        // justify-content: space-around;
        // flex-direction: column;
        // align-items: center;
        // overflow: auto;
        flex: 1 0 auto;
        justify-content: space-around;
        align-items: center;
        // position: absolute;
        // width: 100%;
        // top: 0;
        // left: 0;

        &::before {
          content: ' ';
          display: block;
          background: #cddee3;
          width: 100%;
          height: 1px;
          position: absolute;
          left: 0;
          top: 417px;
          z-index: 1;

          @media @WXGA-display {
            top: 356px;
          }

          @media @mobile {
            display: none;
          }
        }

        @media @mobile {
          align-items: flex-start;
          flex-direction: column;
        }


        & > div {
          flex: 4;
          z-index: 1;

          @media @mobile {
            min-height: 280px;
            overflow: scroll;
          }
        }

        ul.portfolios {
          display: flex;
          flex-direction: column;
          width: 20%;
          z-index: 1;
          position: relative;
          transition: top .1s linear;

          min-height: 100%;
          margin-top: 300px;
          // margin-top: -70px;
          // margin-top: 34px;
          // margin-top: 145px;
          // z-index: 1;
          // margin-left: 50px;

          @-moz-document url-prefix() {
            margin-top: 70px;
          }

          
          @media @WXGA-display {
            margin-top: 348px;
          }

          @media @mobile {
            display: none;
          }

          li {
            z-index: 1;
            margin-top: 20px;

            @media @WXGA-display {
              margin-top: 10px;
              padding: 6px 0;
            }

            a {
              color: #000;
              font-family: 'Brandon Black';
              font-size: 14px;
              text-decoration: none;
              text-transform: uppercase;
              letter-spacing: 0.025em;

              @media @WXGA-display {
                font-size: 12px;
                letter-spacing: 0.05em;
              }

              &:hover {
                color: #ff7d00;
              }
            }

            &.active {
              a {
                color: #ff7d00;
              }
            }
          }
        }

        .article-holder {
          z-index: 1;
        }

        article {
          display: flex;
          width: 80%;
          flex-direction: column;
          margin-top: -300px;
          flex-shrink: 0;
          flex-direction: row;
          justify-content: space-between;
          z-index: 1;
          display: none;
          transition: all 1s linear;

          @-moz-document url-prefix() {
            margin-top: -450px;
          }
          // position: relative;
          // transition: all 1s linear;
          // top: 65px;
          // left: 0;

          &.active {
            display: flex;
          }

          @media @WXGA-display {
            margin-top: -70px;
          }

          @media @mobile {
            margin-top: 10px;
            width: 100%;
            flex-direction: column;
          }


          .portfolio-transition {
            // opacity: 1;
            // transition: all .2s linear;
            // &.out {
            //   opacity: 0;
            // }
          }

          .content {
            display: flex;
            margin-left: 40px;
            flex-direction: column;
            height: 371px;
            justify-content: space-between;

            @media @WXGA-display {
              margin-left: 0px;
            }

            @media @mobile {
              height: 100%;
              margin-left: 0;
              order: 1;
            }

            div {
              flex: 5 4 50%;
              display: flex;

              @media @mobile {
                flex: 0;
              }

              h4 {
                color: #009ec0;
                font-family: 'Brandon Black';
                font-size: 34px;
                margin: 0;
                align-self: center;
                text-transform: uppercase;

                @media @WXGA-display {
                  letter-spacing: 0.025em;
                  font-size: 29px;
                  margin-top: 18px;
                }

                @media @mobile {
                  margin: 5px 0;
                  font-size: 22px;
                  order: 0;
                }
              }
            }

            .skill-items {
              flex: 1 16 100%;
              display: flex;
              flex-wrap: wrap;
              counter-reset: my-badass-counter;
              position: relative;
              right: 20px;

              @media @WXGA-display {
                flex: 1 7 100%;
                right: -10px;
              }

              li {
                font-family: "Brandon Bold";
                font-size: 55px;
                margin: 5px 20px;

                @media @WXGA-display {
                  font-size: 42px;
                  margin: 3px 30px 0 32px;
                }

                @media @mobile {
                  font-size: 24px;
                  margin: 10px 25px;
                }

                &:before {
                  font-family: "PFDinTextPro-Regular";
                  margin-right: 5px;
                  font-size: 29px;
                  position: relative;
                  top: -5px;
                  color: #a8d0da;

                  content: counter(my-badass-counter);
                  counter-increment: my-badass-counter;

                  @media @WXGA-display {
                    font-size: 23px;
                    top: -14px;
                    left: -9px;
                  }

                  @media @mobile {
                    font-size: 16px;
                  }

                }
              }
              // font-family: 'Proxima Nova';
              // color: #525a5d;
              // line-height: 1.7;
              // width: 560px;
              //
              // @media @mobile {
              //   flex: 0;
              //   order: 2;
              //   width: auto;
              //   font-size: 12px;
              // }
            }
          }

          .image {
            align-self: center;
            display: flex;
            flex-direction:column;
            align-items: flex-start;
            margin: 0 auto;

            @media @mobile {
              display: none;
            }

            img {
              max-width:100%;
              max-height:100%;
            }
          }
        }

        .controls {
          display: none;

          @media @mobile {
            display: flex;
            justify-content: center;
            flex: 4;
            align-self: center;

            a.arrow {
              transition: color .2s linear;
              cursor: pointer;
              -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
              height: 30px;
              &:hover > img {
                // filter: ~"grayscale(100%) brightness(0)";
              }
            }
          }
        }
      }
    }
  }
}
